body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.todoCounter{
    font-size: 20px;
    text-align: center;
}
.todo-add{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: none;
    font-size: 34px;
    background-color: rgba(149, 149, 243);
    position:fixed;
    bottom: 42px;
    right:42px;
    z-index: 1;
}
.searchbar{
    border:none;
    padding:6px;
    border-radius: 8px;
    box-shadow: 1px 1px 1px 1px;
    text-align: center;
}
.todoContainer{
  width:70%;
  min-width: 100px;
  max-width: 900px;
  display: flex;
  flex-direction: column;
  grid-gap:24px;
  gap:24px;
  margin: 0 auto;
  padding:32px;
  background-color:rgba(105, 90, 205, 0.776);
  border-radius: 8px;
  
}
#modal:has(div) {
    position: absolute;
    top:0px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width:100%;
    background-color: rgba(159, 157, 157, 0.604);
}
.modal-close{
    position: absolute;
    top:8px;
    right:12px;
    border:3px;
    background-color: rgba(240, 248, 255, 0.901);
    border-radius: 4px;
}
.modal-close:hover{
    background-color: lightcoral;
}
.modal-background{
    position:relative;
    width:80%;
    margin-top:2%;
    max-width: 400px;
    height: 240px;
    background-color:rgba(240, 248, 255, 0.981);
    z-index: 1;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.modal-form{
    width:100%;
    height: inherit;
    display: flex;
    flex-direction: inherit;
    align-items: inherit;
    justify-content: inherit;
}
.modal-info{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.error-msg{
    font-size: 14px;
    color:rgb(161, 54, 54);
}
.modal-title{
    font-size: 16px;
    font-weight: 600;
   
}
.modal-input{
    width:80%;
    border:none;
    border-radius: 8px;
    height:80px;
    background-color: rgba(149, 149, 243, 0.585);
}
.modal-actions-section{
    width:80%;
    height: 10%;
    display: flex;
    justify-content: space-around;
    grid-gap:12px;
    gap:12px;
}
.modal-cancel, .modal-add{
    height: 30px;
    width:80%;
    border: none;
    border-radius: 8px;
}
.modal-cancel{
    background-color: rgb(250, 157, 157);
}
.modal-add{
    background-color: rgba(105, 90, 205, 0.776);
}
.add-disabled{
    background-color: rgba(255, 255, 255, 0.776); 
    border:2px solid rgba(105, 90, 205, 0.776);
}


    

.todo-item{
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    background-color: aliceblue;
    padding:12px 20px;
}
.todo-completed{
    background-color: rgba(240, 248, 255, 0.806);
}
.todo-completed > p{
    text-decoration:line-through;
}
.todo-checkmark{
    background-color: lightgreen;
    padding:10px;
    border-radius: 60%;
    font-size: 12px;
    color:black;
}
.todo-cancel{
    background-color: lightcoral;
    padding:10px;
    border-radius: 60%;
    font-size: 12px;
    color:black;  
}
html{
  box-sizing: border-box;
}
body{
  background-color: rgba(149, 149, 243, 0.585);
}
.todo-header{
  display: flex;
  flex-direction: column;
  width:70%;
  min-width: 100px;
  max-width: 850px;
  align-items: center;
  margin: 24px auto;
  padding:12px;
  background-color:rgba(105, 90, 205, 0.776);
  border-radius: 8px;
}
